import { InjectionToken } from '@angular/core';
export interface INswhpAuthServiceOptions {
  vip: {
    stepUpPath: string,
    domain: string
  };
  adalConfig: adal.Config[];
  adal: {
    pushUrl: string,
    otpUrl: string,
    sendOtpUrl: string,
    registerUrl: string,
  };
}
export const NSWHP_AUTH_CONFIG = new InjectionToken<INswhpAuthServiceOptions>('NSWHP_AUTH_CONFIG');
