import { Inject, Injectable } from '@angular/core';
import { INswhpAuthServiceOptions, NSWHP_AUTH_CONFIG } from './nswhpauth-config';



@Injectable({
  providedIn: 'root'
})
export class NswhpAuthService {
  public nswhpAuthOptions: INswhpAuthServiceOptions;
  constructor(
    @Inject(NSWHP_AUTH_CONFIG) options: INswhpAuthServiceOptions,
  ) {
    this.nswhpAuthOptions = options;
  }
}
