import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'lib-contact-admin',
  templateUrl: './contact-admin.component.html',
  styleUrls: ['./contact-admin.component.scss', '../../main.css']
})
export class ContactAdminComponent {

  public readonly email: string = 'NSWPATH-TestCatalogue@health.nsw.gov.au';
  public readonly subject: string = 'Requesting Access to the Statewide Test Catalogue';
  public readonly href: string = `mailto:${this.email}?subject=${this.subject}`;
  constructor(private readonly router: Router) { }

  public async redirect() {
    await this.router.navigate(['authentication/login']);
  }
}
