import { Component, OnInit } from '@angular/core';
import { VipService } from '../../services/vip.service';
import { IMfaMessage } from '../../model/mfaMessage';

@Component({
  selector: 'lib-otp',
  templateUrl: './otp.component.html',
  styleUrls: ['./otp.component.scss', '../../main.css']
})
export class OtpComponent implements OnInit {

  submitted = false;
  authenticated = false;
  failed = false;

  submitButtonText = 'Submit';

  constructor(
    private vipService: VipService,
  ) { }

  ngOnInit() {
  }

  onSubmit(code: string): false {

    if (!this.submitted) {
      this.submitted = true;
      this.submitButtonText = 'Submitting...';

      this.vipService.authenticateOtpCode(code).subscribe(
        response => { this.handleSuccessfulOtpAuthentication(response); },
        () => { this.handleFailedOtpAuthentication(); }
      );
    }

    return false; // Don't cause a reload
  }

  private handleSuccessfulOtpAuthentication(response: IMfaMessage) {
    console.debug(response);

    this.authenticated = true;
    this.failed = false;

    // NOTE: VipService intercepts the response and stores the Vip token

    this.vipService.redirectToLastLocation();
  }

  private handleFailedOtpAuthentication() {
    this.submitted = false;
    this.failed = true;
    this.submitButtonText = 'Try Again';
  }

  // Turn the input green again when the user starts changing the otp code
  public enteringOtp() {
    this.failed = false;
  }
}
